@import url(https://fonts.googleapis.com/css?family=Dosis|Montserrat|Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Aguafina+Script|Josefin+Sans|Montserrat|Mukta|Rubik);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.NavBar{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
    width: 100%;
    color: white;
    flex-wrap: wrap;
    /* background-color: rgba(165, 42, 42, 0.582); */
    top: 0px;
    z-index: 10;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .NavBarItem{
    padding: 30px;
    color: white;
    text-shadow: 0px 0px 4px black;
  }

  .NavBarItemSmall{
    flex: 1 1 100%;
    background-color: brown;
    order: 5;
  }

  .NavBarItemHidden{
    display: none;
  }

  .OptionsContainer{
    height: 100%;
    width: 20px;
    padding: 5px 15px;
    border-style: solid;
    border-color: white;
    border-width: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    cursor: pointer;
    /* box-sizing: border-box; */
  }

  .OptionsIcon{
    margin: 2px;
    width: 20px;
    height: 20px; 
  }

  .OpMouseOver{
    background-color: rgba(255, 255, 255, 0.4);
  }

  .OpHidden{
    display: none;
  }

  .TextDisplay{
    font-size: 2.1vw;
  }

  .LinkStyle{
    text-decoration: none;
    color: white;
  }

  .NavBarItem:hover{
    background-color: rgb(255, 255, 117);
  }
.Button{
  padding: 15px 25px;
  background-color: rgb(207, 0, 0);
  border-style: none;
  margin-bottom: 20px;
  margin: 10px;
  color: white;
  font-size: 1em;
  font-family: 'Dosis', sans-serif;
  cursor: pointer;
}
.Header{
    height: 450px;
    position: relative;
}

.BannerOver{
    height: 100%;
    width: 100%;
    position: absolute;
}

.BannerImage{
    object-fit: cover;
}

.BannerOverlay{
    background-color: maroon;
    opacity: 0.5;
}

.BannerStreakOverlay{
    width: 50%;
}

.TitleText{
    font-size: 5.5vw;
    width: 900px;
    display: inline-block;
    color: white;
    font-family: 'Mukta', sans-serif;
    text-shadow: 0 0 10px black;
    line-height: 1.5em;
    text-align: center;
    width: 90%;
}

.ElementsContainer{
    display: flex;
    flex-flow: column;
    align-items: stretch;
    justify-content: center;
}
.Logo{
    width: 50px;
    height: 50px;
    justify-self: flex-start;
    padding-left: 20px;
}
.Red{
  background-color: rgba(255, 176, 176, 0.5);
  border-radius: 5px;
}

.Blue{
  background-color: rgba(172, 172, 255, 0.5);
  border-radius: 5px;
}

.Green{
  background-color: rgba(175, 255, 175, 0.5);
  border-radius: 5px;
}

.SingleInfoContainer{
  position: relative;
  margin: 5px 4px;
  width: 15vw;
  height: 15vw;
  min-width: 100px;
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Cover{
  width: 90%;
  height: 90%;
  position: absolute;
}

.BackImage{
  color: rgba(0, 0, 0, 0.151);
}

.InfoTextStyle{
  font-weight: bolder;
  text-align: center;
  font-size: 2vw;
  text-shadow: 0 0 5px black;
}


.Footer{
    background-color: black;
    min-height: 100px;
  }
.HomePage{
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
}

.InfoContainer{
  display: flex;
  align-items: center;
  justify-content: center;
}


.SubContainer{
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
  }
  
.SubBody{
    display: flex;
    justify-content: start;
    align-items: stretch;
    flex-direction: row;
    flex-wrap: wrap;
}

.InfoCard{
    margin: 50px;
    box-shadow: 2px 2px 10px rgb(187, 187, 187);
    border-radius: 5px;
    padding: 65px 40px;
}

.InfoCardSmall{
    margin: 8px;
    padding: 10px 40px;
}

.InputStyle{
    padding:0px; font-size:1.2em; width:10%; box-sizing:border-box; border-style:none; border-width:0;
    display: inline-block;flex-grow: 1;align-items: flex-end;outline: none;background-color: transparent;
}

.TextAreaStyle{
    padding: 10px; height: 100px;
}

.InputContainer{
    border-width:1px;display: flex;border-color: black;
    border-style: solid;align-items: center;border-radius: 0px;box-shadow: 0px 0px 0px maroon;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.MainContainer{
    display:block; text-align:start; border-style:none;margin:15px 5px;
}

.Focused{
    box-shadow: 0px 0px 3px blue;border-color: rgb(149, 225, 255);
}

.Icon{
    width: 100%;
    height: 100%;
}

.IconFocused{
    color: rgb(30, 191, 255);
    
}

.IconError{
    color: rgb(255, 77, 77);
}

.ErrorDisplay{
    color: red;
    background-color: rgb(255, 194, 194);
    padding: 5px;
    box-sizing: border-box;
    -webkit-transition: height 1s;
    transition: height 1s;
}

.ErrorHidden{
    display:none;
}



.SubInsContainer{
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
}

.SubInsBody{
  height: 1000px;
  display: inline-block;
}
